.App {
  text-align: center;
}

h2 {
  font-style: italic;
  text-shadow: 5px 5px 1.5px #3ab795;
  font-family: 'Sedgwick Ave Display', cursive;
}

h5 {
  font-style: italic;
  text-shadow: 3px 3px 2px #75b9be;
  font-family: 'Sedgwick Ave Display', cursive;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear; 
  height: 20vh;
  width: 15%;
	border-radius: 50%/60% 60% 40% 40%;

	-webkit-box-shadow: 0px 0px 30px #D5D3D3, 0px 0px 30px #D5D3D3;
	box-shadow: 0px 0px 30px #D5D3D3, 0px 0px 30px #D5D3D3; 
}

.signIN {
  text-align: center;
  padding: 2% 10% 15% 10%;
  height: 10vh;
  width: 70%;
  display: inline-block;
  font-family: sans-serif;
  /* font-family: 'Sedgwick Ave Display', cursive; */
}

.buttons {
  position: relative;
  display: inline-block;
  width: 40%;
}
.A {
  width: 17%;
  margin: 2% 1% 0% 0%;
}
.B {
  width: 10%;
  margin: 2% 1% 0% 1%;
}
.C {
  width: 7%;
  margin: 2% 0% 0% 1%;
}

.end {
  display: block;
}

.footer:hover {
  -webkit-animation: shake 0.52s cubic-bezier(.36,.07,.19,.97) both;
          animation: shake 0.52s cubic-bezier(.36,.07,.19,.97) both;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

@-webkit-keyframes shake {
  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }
  
  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

@keyframes shake {
  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }
  
  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

.button {
  margin: 2%;
  display: inline-block;
  position: relative;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes pulse {
  from {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }

  50% {
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
            transform: scale3d(1.05, 1.05, 1.05);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }
}

@keyframes pulse {
  from {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }

  50% {
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
            transform: scale3d(1.05, 1.05, 1.05);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }
}

.pulse {
  -webkit-animation: pulse;
          animation: pulse;
}

i.material-icons {
  position: relative;
  top: 4px;
  display: inline-block;
}


